html,
body{
    height: 100%;
}

/*all*/
.heading{
    font-size: 52px;
}
.writer{
    white-space: pre-line;
}

/* first */
#first{
    position: relative;
    min-height: 100%;
    background: #261969;
}
.first-nav{
    background: #2c2191;
}

#first-down{
    position: absolute;
    left: 0;
    right: 0;
    z-index: 10;
    bottom: 15px;
    color: #fff;
}
.down-circle{
      cursor: pointer;
      display: block;
      margin: 0 auto;
      width: 35px;
      height: 35px;
      border-radius: 50%;
      border: 3px solid #6558d9;
      text-align: center;
}
.down-icon{
    animation: pulsate 1.5s ease;
    animation-iteration-count: infinite;
    padding-top: 7px;
}

/* skills */
.skill-heading{
    font-size: 38px;
}
.skills-card{
    padding: 5px 3px 0 3px;
}
.skills-img{
    height: 100px;
    width: 300px;
}

/* experience */
#experience{
    background: #f2f2f5;
    padding: 5px 0;
}

.experience-next{
    font-size: 40px;
    font-weight: 900;
    color: #2c2191;
}

/* last */
#last{
    background: #261969;
}

.last-img{
    width: 10rem;
    height: 10rem;
}

.last-contact{
    margin-bottom: 2px;
    font-size: 1.2rem;
}

.last-icon{
    padding: 0 5px
}

#contact-form{
    margin-top: 20px;    
}
    #contact-form input,
    #contact-form textarea {
      display: block;
      width: 95%;
      box-sizing: border-box;
      padding: 10px 5px;
      border-radius: 4px;
      border: none;
      margin-bottom: 10px;
      background: #191353;
      color: #fff;
      -webkit-transition: 0.5s ease all;
      -o-transition: 0.5s ease all;
      transition: 0.5s ease all; }
      #contact-form input::-webkit-input-placeholder,
      #contact-form textarea::-webkit-input-placeholder {
        color: #fff; }
      #contact-form input:-moz-placeholder,
      #contact-form textarea:-moz-placeholder {
        color: #fff;
        opacity: 1; }
      #contact-form input::-moz-placeholder,
      #contact-form textarea::-moz-placeholder {
        color: #fff;
        opacity: 1; }
      #contact-form input:-ms-input-placeholder,
      #contact-form textarea:-ms-input-placeholder {
        color: #fff; }
      #contact-form input:focus,
      #contact-form textarea:focus {
        outline: none;
        background: #0c0929; }
    #contact-form textarea {
      height: 10rem;
